<template>
    <div id="inventaire">
        <!-- les liens s'adapteront à l'utilisateur connecté -->
        <nav class="navbar navbar-expand-md navbar-light">
            <span class="navbar-brand mb-0 h1">Gestion des fournitures scolaires</span>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item">
                        <a class="nav-link" href="accueil">Accueil</a>
                    </li>
                    <li class="nav-item dropdown active">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Consulter
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="inv-ele">Inventaire d'un élève</a>
                            <a class="dropdown-item" href="inv-ens">Inventaire d'un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Distribuer
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="distr-cla">A une classe</a>
                            <a class="dropdown-item" href="distr-ele">A un élève</a>
                            <a class="dropdown-item" href="distr-ens">A un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="gerer-fourniture">Gérer stock de fourniture</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="gerer-categorie">Gérer les catégories</a>
                    </li>
                </ul>
                <p class="utilisateur">Nom de l'utilisateur connecté</p>                
                <a href="form_conn" class="btn btn-danger" role="button">Déconnexion</a>
            </div>
        </nav>

        <div class="container-fluid col-md-11">

            <h1>Inventaire enseignant</h1>





            <div class="row col-lg-12 bottom-buffer d-flex flex-row-reverse justify-content-center">
                <div class="col-xl-3 offset-xl-1 col-lg-5 bottom-buffer">
                    <div class="input-group max-500">
                        <input type="text" class="form-control" placeholder="Rechercher un enseignant...">
                        <span class="input-group-btn">
                            <button class="btn" type="submit" id="search-btn"><i class="fa fa-search"></i></button>
                        </span>
                    </div>
                </div>
                <div class="col-xl-3 col-lg-5">
                    <div class="form-group row">
                        <select class="form-control col-md max-500">
                            <option>-- Sélectionner un enseignant --</option>
                            <option>INF1a</option>
                            <option>INF1b</option>
                            <option>INF2b</option>
                            <option>INF3a</option>
                            <option>INF3b</option>
                        </select>
                    </div>
                </div>

            </div>


            <h4>Nom de l'enseignant</h4>

            <div class="table-responsive justify-content-md-center">
                <table class="table table-striped">
                    <thead class="thead-dark">
                        <tr>
                            <th scope="col">Désignation</th>
                            <th scope="col" width="170">Date de reçu</th>
                            <th scope="col" width="140">Qte reçu</th>
                            <th scope="col" width="140">Qte distribué</th>
                            <th scope="col" width="140">Qte à rendre</th>
                            <th scope="col" width="140">Qte rendu</th>
                            <th scope="col" width="200">Prix (CHF)</th>
                            <th scope="col" width="200">Montant (CHF)</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in inventaire" v-bind:key="item">
                            <td>
                                {{item.designation}}
                            </td>
                            <td>
                                18 04 2018
                            </td>
                            <td class="alignerDroite">
                                {{item.quantite}}
                            </td>
                            <td class="alignerDroite">
                                0
                            </td>
                            <td class="alignerDroite">
                                {{item.quantite}}
                            </td>
                            <td>
                                <input type="number" min="0" class="form-control">
                            </td>
                            <td>
                                {{item.prix}}.00
                            </td>
                            <td >
                                {{item.quantite*item.prix}}.00
                            </td>

                        </tr>
                        <tr class="table-active">
                            <td colspan="7" class="alignerDroite">
                                Total :
                            </td>
                            <td colspan="2">
                                {{prixTotal}}.00
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
  name: "inventaire",
  data() {
    return {
      inventaire: [],
      prixTotal: 0,
      errors: []
    };
  },
  methods: {
    calculTotal: function(inventaire_) {
      let total = 0;
      inventaire_.forEach(element => {
        total += element["prix"] * element["quantite"];
      });
      this.prixTotal = total;
    }
  },
  // Fetches posts when the component is created.
  created() {
    axios
      .get(`http://localhost:3000/inventaire`)
      .then(response => {
        // JSON responses are automatically parsed.
        this.inventaire = response.data;
        this.calculTotal(this.inventaire);
      })
      .catch(e => {
        this.errors.push(e);
      });
  }
};
</script>
<!-- styling for the component -->
<style scoped>
.max-500 {
  max-width: 500px;
}

select {
    margin: 0 15px;
}

.alignerDroite {
    text-align: right;
}
</style>


